html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

body {
  margin: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block; }

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden],
template {
  display: none; }

a {
  background-color: transparent; }

a:active,
a:hover {
  outline: 0; }

abbr[title] {
  border-bottom: 1px dotted; }

b,
strong {
  font-weight: bold; }

dfn {
  font-style: italic; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

mark {
  background: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -0.5em; }

sub {
  bottom: -0.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

figure {
  margin: 1em 40px; }

hr {
  box-sizing: content-box;
  height: 0; }

pre {
  overflow: auto; }

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0; }

button {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled],
html input[disabled] {
  cursor: default; }

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

input {
  line-height: normal; }

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto; }

input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: content-box; }

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em; }

legend {
  border: 0;
  padding: 0; }

textarea {
  overflow: auto; }

optgroup {
  font-weight: bold; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

/*--------------------------------------------------------------
# Embedded Fonts
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
  box-sizing: border-box;
  font-size: 16px;
  font-size: 1rem; }

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit; }

body {
  background: url("http://refilter.van.cp.academy.red/wp-content/uploads/2019/09/pattern.jpg") no-repeat center bottom/cover; }

.entry-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }

img {
  height: auto;
  /* Make sure images are scaled correctly. */
  max-width: 100%;
  /* Adhere to container width. */ }

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
/* Fields */
input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
  color: #666;
  border: 1px solid #ccc;
  border-radius: 3px; }
  input[type="text"]:focus,
  input[type="email"]:focus,
  input[type="url"]:focus,
  input[type="password"]:focus,
  input[type="search"]:focus,
  textarea:focus {
    color: #111; }

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"] {
  padding: 3px; }

textarea {
  padding-left: 3px;
  width: 100%; }

.btn-green-full {
  text-decoration: none;
  color: #fff;
  border: 1px solid #17cf54;
  background: #0ed166;
  padding: 0.65rem 1.2rem;
  color: #fff;
  border-radius: 8px;
  font-size: 0.9rem; }

.wp-block-button__link {
  background: #0ed166;
  border-radius: 8px; }

.hfeed.site.container {
  min-width: 1240px;
  position: relative;
  width: 80%;
  margin: 0 auto; }

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/* Links */
.skip-link {
  display: none; }

.site-header {
  display: flex;
  background: transparent;
  width: 100%;
  color: #fff; }
  .site-header a {
    text-decoration: none;
    color: #fff; }

/* Menus */
.main-navigation {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  z-index: 2; }
  .main-navigation .site-branding {
    flex-basis: 30%;
    padding-left: 1.7rem; }
  .main-navigation .navbar-btn {
    flex-basis: 30%;
    text-align: right; }
  .main-navigation .nav-menu {
    padding: 0;
    margin: 0;
    list-style-type: none;
    display: none; }

/* Small menu. */
.menu-toggle {
  border: none;
  background: transparent; }

.main-navigation.toggled .nav-menu {
  background: #fff;
  z-index: 2;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 40vh;
  z-index: -1; }
  .main-navigation.toggled .nav-menu li {
    padding-top: 1.8rem;
    font-size: 1.1rem;
    padding-left: 1.7rem; }
    .main-navigation.toggled .nav-menu li:first-child {
      margin-top: 3rem; }
  .main-navigation.toggled .nav-menu a {
    color: #000; }

@media screen and (min-width: 768px) {
  .menu-toggle {
    display: none; }
  .main-navigation {
    justify-content: space-evenly;
    width: 80%;
    margin: 0 auto; }
    .main-navigation .site-branding {
      order: 1; }
    .main-navigation .menu-menu-1-container {
      order: 2; }
      .main-navigation .menu-menu-1-container .nav-menu {
        display: flex; }
        .main-navigation .menu-menu-1-container .nav-menu li {
          padding-right: 1rem; }
    .main-navigation .navbar-btn {
      order: 3;
      text-align: left; } }

